<template>
  <div class="prompt">
    <p>list <strong>{{msg.length}}</strong> </p>
    <p> <input type="text" v-model="inputVal" class="inputV"> </p>
    <button v-on:click="deleteLast" class="yes"></button>
  </div>
</template>

<script>
export default {
// 
  data: function () {
    return {
      inputVal: this.msg.length
    }
  },
  props: ['msg'],
  methods: {
    deleteLast: function () {
      // console.log(this.msg.length-1);
      // 
      this.$emit('prompt', this.inputVal - 1)
    }
  },
  watch: {
    msg: function () {
      this.inputVal = this.msg.length
    },
    inputVal: function (val) {
      this.inputVal = Number(val.toString().replace(/[^0-9]/g, ''))
      // console.log(this.inputVal,typeof this.inputVal);
      if (this.inputVal > this.msg.length) {
        this.inputVal = this.msg.length
      }
    }
  },
  /* computed:{
    inputVal:function(){
      return this.msg.length
      }
    }, */
  filters: {
    valFilter: function () {

    }
  }
}
</script>
<style scoped>
  .prompt {
    color: #ff9b26;
  }

  .prompt strong {
    color: #ff6815
  }

  .yes {
    display: inline-block;
    width: 30px;
    height: 30px;
    border: none;
    background: #ff706d;
    color: #fff;
    font-size: 18px;
  }

  .inputV {
    width: 20px;
  }
</style>



